import { Component, OnInit } from '@angular/core';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { RequestService } from 'src/app/api/request.service';
import { ActivatedRoute } from '@angular/router';
import { Model } from 'src/app/api/models/model';

@Component({
  selector: 'app-events-player',
  templateUrl: './events-player.page.html',
  styleUrls: ['./events-player.page.scss'],
})
export class EventsPlayerPage implements OnInit {

  public id ;
  public event ; 
  public player;
  public modelPlayer : Model;

  constructor(
    public route : ActivatedRoute,
    public requestService : RequestService,
    public dialogService : DialogService,
    public utilArray : UtilArrayService
  ) { 
    this.id = route.snapshot.paramMap.get('id');
    this.event = route.snapshot.paramMap.get('event');
    this.modelPlayer = new Model('Player',requestService);
  }

  ngOnInit() {
    this.init();


  }

  async init(){
    let parms = {};
    if(this.event){
      parms['event'] = this.event;
    }

    await this.dialogService.loadingProgres();

    this.modelPlayer.api_functionModel(this.id,'pageEvents',parms).subscribe(
      response =>{

        if(response['status'] == 'success' ){
          this.player = response['data']
        }

        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
      }
    )
  }

}
